***********/
.room_image img {
    width:  100%;
}

@import url('https://fonts.googleapis.com/css?family=Montserrat:700|Open+Sans:300,400,600,700');

/*********************************
2. Body and some general stuff
*********************************/


ul
{
	list-style: none;
	margin-bottom: 0px;
}
p
{
	font-family: 'Open Sans', sans-serif;
	font-size: 14px;
	line-height: 2;
	font-weight: 400;
	color: #838383;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
	text-shadow: rgba(0,0,0,.01) 0 0 1px;
}
p a
{
	display: inline;
	position: relative;
	color: inherit;
	border-bottom: solid 1px #ffa07f;
	-webkit-transition: all 200ms ease;
	-o-transition: all 200ms ease;
	transition: all 200ms ease;
}
a, a:hover, a:visited, a:active, a:link
{
	text-decoration: none;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
	text-shadow: rgba(0,0,0,.01) 0 0 1px;
}
p a:active
{
	position: relative;
	color: #FF6347;
}
p a:hover
{
	color: #FFFFFF;
	background: #ffa07f;
}
p a:hover::after
{
	opacity: 0.2;
}
::-moz-selection
{

}
::selection
{

}
p::-moz-selection
{

}
p::selection
{

}
h1{font-size: 48px;}
h2{font-size: 36px;}
h3{font-size: 24px;}
h4{font-size: 18px;}
h5{font-size: 14px;}
h1, h2, h3, h4, h5, h6
{
	font-family: 'Open Sans', sans-serif;
	-webkit-font-smoothing: antialiased;
	-webkit-text-shadow: rgba(0,0,0,.01) 0 0 1px;
	text-shadow: rgba(0,0,0,.01) 0 0 1px;
}
h1::-moz-selection,
h2::-moz-selection,
h3::-moz-selection,
h4::-moz-selection,
h5::-moz-selection,
h6::-moz-selection
{

}
h1::selection,
h2::selection,
h3::selection,
h4::selection,
h5::selection,
h6::selection
{

}
.form-control
{
	color: #db5246;
}
section
{
	display: block;
	position: relative;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}
.clear
{
	clear: both;
}
.clearfix::before, .clearfix::after
{
	content: "";
	display: table;
}
.clearfix::after
{
	clear: both;
}
.clearfix
{
	zoom: 1;
}
.float_left
{
	float: left;
}
.float_right
{
	float: right;
}
.trans_200
{
	-webkit-transition: all 200ms ease;
	-o-transition: all 200ms ease;
	transition: all 200ms ease;
}
.trans_300
{
	-webkit-transition: all 300ms ease;
	-o-transition: all 300ms ease;
	transition: all 300ms ease;
}
.trans_400
{
	-webkit-transition: all 400ms ease;
	-o-transition: all 400ms ease;
	transition: all 400ms ease;
}
.trans_500
{
	-webkit-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	transition: all 500ms ease;
}
.fill_height
{
	height: 100%;
}
.super_container
{
	width: 100%;
	overflow: hidden;
}
.prlx_parent
{
	overflow: hidden;
}
.prlx
{
	height: 130% !important;
}
.nopadding
{
	padding: 0px !important;
}

/*********************************
3. Home
*********************************/

.home
{
	width: 100%;
	height: 85vh;
}
.home_slider_container
{
	width: 100%;
	height: 100%;
}
.home_slider
{
	width: 100%;
	height: 100%;
}
.home_slider_item
{
	width: 100%;
	height: 100%;
}
.home_slider_background
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
}
.home_slider_content_container
{
	width: 100%;
	height: 100%;
	padding-top: calc(39vh - 25px);
	padding-left: 30px;
	padding-right: 30px;
}
.home_slider_content
{
	width: 100%;
	max-width: 900px;
	height: 100%;
	margin: 0 auto;

}
.home_slider_content h1
{
	font-size: 92px;
	font-weight: 700;
	text-transform: uppercase;
	color: #FFFFFF;
	text-shadow:  0px 0px 41px #444;
}
.home_slider_nav
{
	position: absolute;
	top: 53.9%;
	left: 0;
	width: 140px;
	height: 64px;
	background: #fd784f;
	z-index: 10;
	cursor: pointer;
}
.home_slider_nav::after
{
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: #0e1d41;
	content: '';
	z-index: -1;
	-webkit-transition: all 600ms cubic-bezier(.25,.62,.27,.99);
	-o-transition: all 600ms cubic-bezier(.25,.62,.27,.99);
	transition: all 600ms cubic-bezier(.25,.62,.27,.99);
}
.home_slider_nav:hover::after
{
	width: 0px;
}
.home_slider_nav_left
{
	padding-right: 28px;
}
.home_slider_nav img
{
	width: 12px;
	height: 13px;
}
.flipInX
{
	-webkit-animation-delay: 600ms;
	        animation-delay: 600ms;
}

/*********************************
4. Header
*********************************/

.header
{
	display: block;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 150px;
	z-index: 15;
}
.header.scrolled
{
	height: 120px;
	/* background: #0e1d41; */
	border: none;
}

.header.scrolled .header_container
{
	height: 120px;
}

