4.2 Main Navigation
*********************************/

/*********************************
4.3 Phone Home
*********************************/

.phone_home
{
	width: auto;
	height: 44px;
	background: #fd784f;
	padding-left: 16px;
	padding-right: 9px;
}
.phone_home span
{
	font-size: 18px;
	font-weight: 600;
	line-height: 44px;
	color: #FFFFFF;
}

/*********************************
4.4 Hamburger
*********************************/

.hamburger_container
{
	display: none;
}
.hamburger i
{
	font-size: 20px;
	padding: 5px;
	color: #3c85cd;
	cursor: pointer;
}
.hamburger i:hover
{
	color: #f2f2f2;
}
.hamburger i:active
{
	color: #fd784f;
}

/*********************************
5. Menu
*********************************/

.menu
{
	display: block;
	width: 100%;
	max-height: 0px;
	overflow: hidden;
	opacity: 0;
	visibility: hidden;
	background: #FFFFFF;
	-webkit-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	transition: all 500ms ease;
}
.menu.active
{
	opacity: 1;
	visibility: visible;
}
.menu_item
{
	border-bottom: solid 1px #f2f2f2;
}
.menu_item:last-child
{
	border: none;
}
.menu_item:hover
{
	background: #f2f2f2;
}
.menu_item a
{
	display: block;
	font-size: 14px;
	font-weight: 700;
	text-transform: uppercase;
	color: #0e1d41;
	padding-top: 5px;
	padding-bottom: 5px;
	line-height: 60px;
}

/*********************************
6. Search Box
*********************************/

.search_box
{
	width: 100%;
	height: auto;
	z-index: 10;
}
.search_box_outer
{
	margin-top: -161px;
	width: 100%;
}
.search_box_inner
{
	width: 100%;
	background: #0e1d41;
	padding-top: 32px;
	padding-bottom: 25px;
}

/*********************************
6.1 Search Box Title
*********************************/

.search_box_title
{
	position: absolute;
	bottom: 100%;
	left: 50%;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);
	width: 43.97%;
	height: 51px;
}
.search_box_title_inner
{
	width: 100%;
	height: 100%;
	background: #fd784f;
}
.search_box_title_icon
{
	position: absolute;
	left: 0;
	top: 0;
	width: 51px;
	height: 51px;
	background: #0e1d41;
}
.search_box_title_icon img
{
	display: block;
	position: relative;
	width: 20px;
	height: 20px;
}
.search_box_title_inner span
{
	font-size: 16px;
	font-weight: 600;
	line-height: 51px;
	color: #FFFFFF;
	text-transform: uppercase;
	margin-left: 7%;
}

/*********************************
6.2 Search Box Arrow
*********************************/

.search_arrow_box
{
	position: absolute;
	left: -29px;
	bottom: calc(100% - 40px);
	pointer-events: none;
}
.search_arrow_box_inner img
{
	width: 382px;
	height: 278px;
}
.search_arrow_circle
{
	position: absolute;
	top: 0;
	left: 0;
	width: 125px;
	height: 125px;
	background: #fd784f;
	border-radius: 50%;
	overflow: hidden;
}
.search_arrow_circle span
{
	font-size: 14px;
	font-weight: 600;
	color: #FFFFFF;
}

/*********************************
6.3 Search Form
*********************************/

.search_form
{
	width: 100%;
}
.search_box_container
{
	width: 100%;
	padding-left: 20px;
	padding-right: 20px;
}
.search_box_container:nth-child(2)
{
	margin-top: 6px;
}
.dropdown_row
{
	display: block;
	width: 100%;
}
.dropdown_item
{
	display: block;
	margin-left: 10px;
	margin-right: 10px;
	margin-bottom: 10px;
	float: left;
}
.dropdown_item_5
{
	width: calc((100% - 100px) / 5);
}
.dropdown_item_6
{
	width: calc((100% - 238px) / 6);
}
.dropdown_item_title
{
	font-size: 13px;
	font-weight: 400;
	margin-bottom: 5px;
	color: #FFFFFF;
	padding-left: 1px;
}
.dropdown_item_select
{
	width: 100%;
	height: 34px;
	background: #FFFFFF;
	-webkit-appearance:none;
    -moz-appearance:none;
    -ms-appearance:none;
    -o-appearance:none;
    appearance:none;
	-webkit-box-shadow: 0px 0px 0px rgba(0, 0, 0, 0);
	-webkit-padding-end: 34px;
	-webkit-padding-start: 15px;
	-webkit-user-select: none;

	background-position: center right;
	background-repeat: no-repeat;
	border: solid 1px #FFFFFF;
	color: #838383;
	font-family: 'Open Sans', sans-serif;
	font-size: 12px;
	margin: 0;
	overflow: hidden;
	padding-top: 2px;
	padding-bottom: 2px;
	-o-text-overflow: ellipsis;
	   text-overflow: ellipsis;
	white-space: nowrap;
	cursor: pointer;
}
.dropdown_item_select::-ms-expand
{
    display: none;
}
.dropdown_item_select:hover
{
	border: solid 1px #fd784f;
}
.dropdown_item_select:focus
{
	outline-color: #fd784f;
}
.search_button
{
	top: 24px;
}
.search_submit_button
{
	width: 97px;
	height: 34px;
	background: #fd784f;
	border: none;
	font-size: 11px;
	font-weight: 400;
	color: #FFFFFF;
	line-height: 34px;
	text-transform: uppercase;
	cursor: pointer;
}
.search_features_container
{
	padding-left: 30px;
	padding-right: 30px;
	margin-top: 8px;
}
.search_features_trigger
{
	display: inline-block;
	width: auto;
}
.search_features_trigger a
{
	font-size: 12px;
	font-weight: 400;
	color: #FFFFFF;
	padding-left: 18px;
}
.search_features_trigger a::before
{
	display: block;
	position: absolute;
	top: 7px;
	left: 0;
	width: 10px;
	height: 10px;
	content: '+';
	font-family: 'Open Sans', sans-serif;
	font-size: 13px;
	line-height: 8px;
	color: #FFFFFF;
	border: solid 1px #FFFFFF;
	padding-left: 1px;
}
.search_features_trigger.active a::before
{
	content: '-';
	padding-left: 2px;
}
.search_features
{
	display: block;
	width: 100%;
	max-height: 0px;
	overflow: hidden;
	opacity: 0;
	-webkit-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	transition: all 500ms ease;
}
.search_features.active
{
	margin-top: 35px;
	opacity: 1;
}
.search_features_item
{
	width: 20%;
	float: left;
	margin-bottom: 10px;
}
.search_features_item div
{
	display: inline-block;
	cursor: pointer;
}
.search_features_item div:hover .search_features_cb
{
	border: 1px solid #fd784f;
}
.search_features_checkbox
{
	cursor: pointer;
}
.search_features_cb
{
	width: 12px;
	height: 12px;
	-webkit-appearance: none;
	-moz-appearance: none;
	-ms-appearance: none;
	-o-appearance: none;
	appearance: none;
	background-color: #FFFFFF;
	border: 1px solid #FFFFFF;
	padding: 9px;
	border-radius: 0px;
	display: inline-block;
	position: relative;
	cursor: pointer;
}
.search_features_cb:focus
{
	outline: none;
	border: 1px solid #fd784f;
}
.search_features_cb:checked::after
{
	display: block;
	position: absolute;
	top: 2px;
	left: 2px;
	width: calc(100% - 4px);
	height: calc(100% - 4px);
	content: '';
	background: #fd784f;
}
.search_features label
{
	display: inline-block;
	position: relative;
	padding-left: 3px;
	margin-bottom: 0px;
	cursor: pointer;
	color: #FFFFFF;
}

/*********************************
7. Section Title
*********************************/

.section_title
{
	padding-top: 37px;
	margin: 0 auto;
	position: relative;
}

.section_title h3
{
	text-transform: uppercase;
	color: #1e2c4e;
