.room_tag a
{
	display: block;
	color: #FFFFFF;
	padding-left: 13px;
	padding-right: 13px;
	padding-top: 1px;
	padding-bottom: 2px;
}
.featured_card_container:hover .featured_card_box
{
	-webkit-box-shadow: 0px 40px 40px rgba(0,0,0,0.55);
	        box-shadow: 0px 40px 40px rgba(0,0,0,0.55);
}
.featured_card_box img
{
	width: 30px;
	height: 30px;
}
.featured_card_box_content
{
	padding-left: 28px;
}
.featured_card_price_title
{
	font-size: 12px;
	font-weight: 400;
	color: #FFFFFF;
}
.featured_card_price
{
	font-size: 24px;
	font-weight: 600;
	color: #FFFFFF;
	margin-top: -3px;
}

