/*********************************
11. Cities
*********************************/

.cities
{
	padding-top: 100px;
	padding-bottom: 100px;
}
.cities_background
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-image: url(../images/world.png);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center center;
	background-attachment: fixed;
}
.cities_slider_container
{
	width: 100%;
	margin-top: 72px;
}
.city_image::after
{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	opacity: 0;
	content: '';
	background: #1e2c4e;
	-webkit-transition: all 200ms ease;
	-o-transition: all 200ms ease;
	transition: all 200ms ease;
}
.city_item:hover .city_image::after
{
	opacity: 0.9;
}
.city_image img
{
	width: 100%;
}
.city_name
{
	position: absolute;
	left: 50%;
	-webkit-transform: translateX(-50%);
	-ms-transform: translateX(-50%);
	transform: translateX(-50%);
	bottom: 21px;
	width: 100%;
	height: 34px;
	background: #fd784f;
	text-align: center;
}
.city_name span
{
	font-size: 14px;
	font-weight: 600;
	color: #FFFFFF;
	line-height: 34px;
	text-transform: uppercase;
}
.city_details
{
	position: absolute;
	top: 45%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	visibility: hidden;
	opacity: 0;
	-webkit-transition: all 200ms ease;
	-o-transition: all 200ms ease;
	transition: all 200ms ease;
}
.city_item:hover .city_details
{
	visibility: visible;
	opacity: 1;
}
.cities_nav
{
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
	width: 48px;
	height: 51px;
	background: #e1e5ec;
	cursor: pointer;
	z-index: 10;
}
.cities_nav:hover
{
	background: #fd784f;
}
.cities_prev
{
	left: -165px;
}
.cities_next
{
	right: -165px;
}

